const routes = [
  {
    path: '/',
    component: () => import('layouts/MainLayout.vue'),
    children: [
      {
        path: '',
        component: () => import('src/pages/HomePage.vue'),
        meta: { showHeader: true }
      },
      {
        path: 'room',
        component: () => import('pages/RoomPage.vue'),
        meta: { showBackArrow: true }
      },
      {
        path: 'instrument',
        component: () => import('pages/InstrumentPage.vue'),
        meta: { showHeader: false }
      },
      {
        path: 'impressum',
        component: () => import('pages/ImpressumPage.vue'),
        meta: { showBackArrow: true, hideFooter: true }
      },
      {
        path: 'debug',
        component: () => import('pages/DebugInfo.vue'),
        meta: { showBackArrow: true, hideFooter: true }
      }
    ]
  },
  {
    path: '/desktop',
    component: () => import('layouts/DesktopLayout.vue'),
    children: [
      { path: '', component: () => import('src/pages/desktop/HomePage.vue') },
      { path: '/desktop/room', component: () => import('pages/RoomPage.vue') },
      { path: '/desktop/instruments/:filter', component: () => import('pages/desktop/InstrumentsPage.vue') },
    ]
  },
  {
    path: '/:catchAll(.*)*',
    component: () => import('pages/ErrorNotFound.vue')
  }
]

export default routes
